import {  
    mapMutations  
} from 'vuex';

export default{
	data(){
		return {
			username:'',
			password:'',
			showPassword: true,
			logining: false,
		}
	},
	onLoad(){
		console.log(uni.getStorageSync('cbe_retoken'));
	},
	methods: {
		...mapMutations(['register','login']),
		inputChange(e){
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		changePassword() {
			this.showPassword = !this.showPassword;
		},
		toPhone(){
			uni.redirectTo({
				url:"phonelogin"
			})
		},
		toRegister(){
			uni.redirectTo({
				url:"zhecexieyi"
			})
		},
		toLogin(){
			this.logining = true;
			if(!this.username){
				uni.showToast({ title: '手机号码/账号不能为空', icon: 'none' });
				this.logining = false;return false;
			}
			if(!this.password){
				uni.showToast({ title: '密码不能为空', icon: 'none' });
				this.logining = false;return false;
			}
			uni.showLoading({
				title: '正在登录',
				mask: true
			});
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/login/pwdlogin',
				data: {username:this.username,password:this.password,usertoken:uni.getStorageSync('cbe_retoken')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					uni.hideLoading();
					var one = resa.data;
					if(one.code==0){
						uni.setStorageSync('cbe_token',one.token);
						this.phonetoken = '';
						uni.showToast({
							title:one.msg,icon:'success',mask:true,duration: 3000
						});
						this.login(one.data);
						var timer = setTimeout(()=>{
							uni.navigateBack({
								delta:1
							})
						},3000);
					}else{
						uni.showToast({ title:one.msg, icon: "none" });
						this.logining = false;
					}
				}
			})
		}
	}
}
